<template>
  <main>
    <div id="header">
      <profile-info :name="username"></profile-info>
      <menu-button></menu-button>
      <wins-counter :value="winsCount"></wins-counter>
      <balance-info :value="balance"></balance-info>
    </div>
    <game-board v-if="isBoardShowing"></game-board>
    <menu-popup v-if="isMenuPopupShowing"></menu-popup>
    <div id="footer">
      <bet-selection></bet-selection>
      <button id="start-game-with-computer" v-on:click="startGameWithComputer"
              v-if="!inSearch">{{ $t('playWithComputer') }}</button>
      <button id="start-game-with-human" v-on:click="startGameWithHuman"
              v-if="!inSearch">{{ $t('playWithHuman') }}</button>
      <button id="cancel-search" v-on:click="cancelSearch"
              v-if="inSearch">{{ $t('cancelSearch') }}</button>
    </div>
  </main>
</template>

<script>

  import GameBoard from "@/components/Home/GameBoard";
  import ProfileInfo from "@/components/Home/ProfileInfo";
  import WinsCounter from "@/components/Home/WinsCounter";
  import BetSelection from "@/components/Home/BetSelector";
  import BalanceInfo from "@/components/Home/BalanceInfo";
  import MenuPopup from "@/components/Home/MenuPopup";
  import MenuButton from "@/components/Home/MenuButton";

  const TicTacToe = require('../services/tictactoe');

  export default {
    name: "Home",
    components: {
      MenuButton,
      BetSelection, WinsCounter, MenuPopup, BalanceInfo,
      GameBoard, ProfileInfo
    },
    data() {
      return {
        game: null,
        isBoardShowing: false,
        isMenuPopupShowing: false,
        inSearch: false,
        winsCount: 999,
        balance: 99,
        username: 'Test'
      }
    },
    mounted() {
      this.game = new TicTacToe(this.$refs['board-table']);
      this.isBoardShowing = this.game.isBoardShowing;
    },
    methods: {
      startGameWithComputer() {

      },
      startGameWithHuman() {
        this.inSearch = true;
        this.game.createLobby();
      },
      cancelSearch() {
        this.inSearch = false;
      }
    }
  }
</script>

<style scoped>
  main {
    /*background: linear-gradient(180deg, #203657 0%, #0B1633 100%);*/
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 5px;
  }
  #header {
    display: flex;
    flex-flow: wrap;
  }

  #footer {
    margin-top: auto;
    margin-bottom: 0px;
  }
  #start-game-with-computer {
    width: calc(100% - 10px);
    height: 64px;
    background: rgb(171, 109, 232);

    box-shadow: 0px 10px 60px 0px rgba(125, 20, 208, 0.8);
  }
  #start-game-with-human {
    width: calc(100% - 10px);
    height: 64px;
    background: rgb(103, 187, 214);

    box-shadow: 0px 10px 60px 0px rgba(41, 142, 174, 0.8);
  }
  #cancel-search {
    width: calc(100% - 10px);
    height: 64px;
    background: rgb(171, 109, 232);

    box-shadow: 0px 10px 60px 0px rgba(125, 20, 208, 0.8);
  }
  button {
    border: none;
    border-radius: 14px;
    margin: 5px 5px;
    color: white;
    font-weight: 600;
    cursor: pointer;
    font-size: 20px;
  }
</style>

<i18n>
  {
    "en": {
      "playWithHuman": "Play",
      "playWithComputer": "Play with computer",
      "cancelSearch": "Cancel search"
    },
    "ru": {
      "playWithHuman": "",
      "playWithComputer": "  ",
      "cancelSearch": " "
    }
  }
</i18n>