const { io } = require("socket.io-client");
const Config = require("../utils/Config").default.data;

class TicTacToe {
  $boardTable = null;
  gameId = null;
  turn = null;
  i = false;
  interval = null;
  status = null;
  stats = null;
  socket = null;
  isBoardShowing = false;
  isTimerPanelShowing = false;

  constructor ($boardTable) {
//    nodejs  socket.io
    this.$boardTable = $boardTable;

    const socket = this.socket = io.connect(Config.api.sockets.baseURL);

    /*$('#reload').hide().button({icons: {primary: 'ui-icon-refresh'}}).click(function () {
      $('#reload').off('click').click(function () {
        window.location.reload();
      });
      socket.emit('start');
    });*/

    socket.on('connect', () => {
      this.status = 'connect';
      // $('#reload').show();
    });
    socket.on('reconnect', () => {
      // $('#reload').show();
      this.status = 'reconnect';
    });
    socket.on('reconnecting', () => {
      // $('#reload').hide();
      this.status = 'reconnecting';
    });
    socket.on('error', (e) => {
      this.status = 'error';
    });
    //  
    socket.on('wait', () => {
      this.status = 'wait';
    });
    //  
    socket.on('exit', () => {
      this.endGame(TicTacToe.turn, 'exit');
    });
    //    
    socket.on('timeout', (turn) => {
      this.endGame(turn, 'timeout');
    });
    //    ,  
    socket.on('ready', (gameId, turn, x, y) => {
      //$('#status').html('   !  ! ' + (turn == 'X' ? '   ' : '  ') + '!');
      this.status = 'ready';

      this.startGame(gameId, turn, x, y);
      // $('#stats').append($('<div/>').attr('class', 'turn ui-state-hover ui-corner-all').html(' : <b>' + (turn == 'X' ? '' : '') + '</b>'));
      /*$("#board-table td").click(function (e) {
        if (TicTacToe.i) socket.emit('step', TicTacToe.gameId, e.target.id);
      }).hover(function () {
        //$(this).toggleClass('ui-state-hover');
      }, function () {
        // $(this).toggleClass('ui-state-hover');
      });*/
    });
    //  
    socket.on('step', function (id, turn, win) {
      //console.info('step', id, turn, win);
      TicTacToe.move(id, turn, win);
    });
    // 
    socket.on('stats', function (arr) {
      this.stats = arr;
    });
  }
  createLobby() {
    this.socket.emit('start');
  }
  startGame(gameId, turn, x, y) {
    this.gameId = gameId;
    this.turn = turn;
    this.i = (turn == 'X');

    this.$boardTable.innerHTML = '';

    //       Vue.js API
    for (var i = 1; i <= y; i++) {
      var tr = document.createElement('tr');
      for (var j = 0; j < x; j++) {
        var td = document.createElement('td');
        td.id = (j + 1) + 'x' + i;
        td.classList.add('ui-state-default');
        td.innerHTML = '&nbsp;';
        tr.appendChild(td);
      }
      this.$boardTable.appendChild(tr);
    }
    // $("#board,#timerpanel").show();
    this.isBoardShowing = true;
    this.isTimerPanelShowing = true;
    this.mask(!this.i);
  }

  mask(state) {
    /*var mask = $('#masked'), board = this.$boardTable;
    clearInterval(this.interval);
    $('#timer').html(15);
    this.interval = setInterval(function () {
      var i = parseInt($('#timer').html());
      i--;
      $('#timer').html(i);
    }, 1000);
    if (state) {
      mask.show();
      var p = board.position();
      mask.css({
        width: board.width(),
        height: board.height(),
        left: p.left,
        top: p.top
      });
    } else {
      mask.hide();
    }*/
  }

  move(id, turn, win) {
    /*this.i = (turn != this.turn);
    $("#" + id).attr('class', 'ui-state-hover').html(turn);
    if (!win) {
      this.mask(!this.i);
      $('#status').html(' ' + (this.i ? ' ' : ' '));
    } else {
      this.endGame(turn, win);
    }*/
  }

  endGame (turn, win) {
    clearInterval(this.interval);
    var text = '';
    switch (win) {
      case 'none':
        text = '!';
        break;
      case 'timeout':
        text = (turn == this.turn ? '  !  !' : '        !  !');
        break;
      case 'exit':
        text = '    !  ';
        break;
      default:
        text = ' ' + (this.i ? '! =(' : '! =)');
    }
    /*$("<div/>").html(text).dialog({
      title: ' ',
      modal: true,
      closeOnEscape: false,
      resizable: false,
      buttons: {
        "  ": function () {
          $(this).dialog("close");
          window.location.reload();
        }
      },
      close: function () {
        window.location.reload();
      }
    });*/
  }
  makeStep() {
    if (TicTacToe.i)
      this.socket.emit('step', TicTacToe.gameId, e.target.id);
  }
}

module.exports = TicTacToe;